.App {
  font-family: sans-serif;
  text-align: center;
} 
button {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}


a {
  color: black;
  text-decoration: none;
}

li{
  list-style: none;
}
.MuiList-root .MuiListItem-root {
  list-style: none;
}


.header-title {
  flex-grow: 1;
  text-align: center;
}

.header {
  margin: 50px;
  display: flex;
  align-items: center;
  font-size: 24px;
}
    
.icon {
  width: 100px;
  height: 100px;
}

.op1 {
  width: 70%;
  height: 70%;
}

.op1s {
  max-width: 100%;
  max-height: 100%;
}
    

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.genderQ {
  width: 30%;
  height: 30%;
}
    
.body {
  margin: 10px;
  font-size: 16px;
}
    
.description {
  margin-left: 10px;
}

.bottom-container {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
}

.bottom-left {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom-right {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  background-color: #f5f5f5;
}

.bottom-button {
  width: 100%;
  max-width: 150px;
  height: 70px;
  border-radius: 0;
}

.footer {
  bottom: 500;
}
 
